import {
  LoadingManager,
  TextureLoader,
  // CubeTextureLoader,
  EquirectangularReflectionMapping,
  SRGBColorSpace,
} from "three";

import { RGBELoader } from 'three/addons/loaders/RGBELoader.js';
import { GLTFLoader } from 'three/addons/loaders/GLTFLoader.js';
// import { DRACOLoader } from 'three/addons/loaders/DRACOLoader.js';
// const dracoLoader = new DRACOLoader();
// dracoLoader.setDecoderPath( 'assets/draco/' );
// dracoLoader.setDecoderConfig( { type: 'js' } );

import LoveFont from '@/assets/fonts/Love.json';
import PPNeueMontrealMedium from '@/assets/fonts/PPNeueMontreal-Medium.json';

class Assets {
  manager = new LoadingManager(); 
  textureLoader = new TextureLoader(this.manager);
  rgbeLoader = new RGBELoader(this.manager);
  gltfLoader = new GLTFLoader(this.manager);
  constructor() {
    // this.gltfLoader.setDRACOLoader( dracoLoader );
  }
  init() {
    const { textureLoader, rgbeLoader } = this;

    this.LoveFont = LoveFont;
    this.PPNeueMontrealMedium = PPNeueMontrealMedium;

    this.worldMap = textureLoader.load( "/assets/map.png" );
    this.alphaMap = textureLoader.load( "/assets/transition4.png" );
    this.map = textureLoader.load( "/assets/Love.png" );
    this.ladyMargot = textureLoader.load( "/assets/lady-margot.jpeg" );
    this.pixel = textureLoader.load( "/assets/pixel.png" );

    this.jocodusTextures = [];
    this.jocodusTextures.push(this.pixel);
    this.jocodusTextures.push(textureLoader.load('/assets/globe-Jodocus-Hondius/L55008745_4.png'));
    this.jocodusTextures.push(textureLoader.load('/assets/globe-Jodocus-Hondius/L55008745_1.png'));
    this.jocodusTextures.push(textureLoader.load('/assets/globe-Jodocus-Hondius/L55008745_3.png'));
    this.jocodusTextures.push(textureLoader.load('/assets/globe-Jodocus-Hondius/L55008745_2.png'));
    this.jocodusTextures.push(this.pixel);
    this.jocodusTextures.forEach(_ => {
      if (_) _.colorSpace = SRGBColorSpace;
      if (_) _.flipY = false;
    })
    this.gltfLoader.load(
      '/assets/globe-Jodocus-Hondius/L55008745.glb',
      ( object ) => {
        this.jocodusGlobe = object.scene;
      },
    );

    this.textureCube = rgbeLoader
      .setPath( '/assets/' )
      .load( 'potsdamer_platz_1k_11zon.hdr',  ( texture ) => {
        texture.mapping = EquirectangularReflectionMapping;
      });
  }
}

export default new Assets();
